import React, { useState } from 'react'
import './Header.css'
import {Link} from 'react-router-dom'

const Header = () =>{
    const [ShowHamburgerMenu,SetHamburgerMenu] = useState(false);

    return (
        <div className="Header">
            <h1 className="Logo">Router Header</h1>
            <div className="Hamburger"  onClick={()=> SetHamburgerMenu(!ShowHamburgerMenu)}>
                <div className="line"></div>
                <div className="line"></div>
                <div className="line"></div>
            </div>
            <ul className={ShowHamburgerMenu ? "Nav ShowMenu" :"Nav HideMenu"}>
                <Link to='/' className="nav-links"><li>Home</li></Link>
                <Link to='/About' className="nav-links"><li>About</li></Link>
                <Link to='/Products' className="nav-links"><li>Products</li></Link>
                <Link to='/Contact' className="nav-links"><li>Contact</li></Link>

            </ul>
        </div>
    );
}

export default Header;